import * as React from 'react';
import { Alert, Button, Layout, Toast } from '../auto-ui';

const Demo: React.FC<any> = () => {
  const onClick = async () => {
    const value = await Alert.async({
      title: '',
      btns: [{ name: '', value: 'cancel' }, { name: '', value: 'ok' }],
    });
    Toast(String(value));
  };

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClick}></Button>
      </Layout.Body>
    </Layout>
  );
};

export default Demo;
