import * as React from 'react';
import { Button, Layout, Toast } from '../auto-ui';

const Demo: React.FC<any> = () => {
  const onClick = () => {
    Toast('5', 5000);
  };

  return (
    <Layout>
      <Layout.Body>
        <Button onClick={onClick}>5</Button>
      </Layout.Body>
    </Layout>
  );
};

export default Demo;
